import React, { useEffect, useState } from "react";
import Calendar from "react-calendar";
import moment from "moment";
import "./Calendar.css"; // css import
import styled from "styled-components";

const HeaderContainer = styled.div`
  display: flex;
  justify-content: center;
  flex-direction: column;
  position: relative;
  cursor: pointer;
  border-bottom: 1px solid #0f4c75;
  height: 96px;
`;
const DayToYear = styled.div`
  font-size: 30px;
  margin: auto;
`;
const DayOfWeek = styled.div`
  font-size: 25px;
  margin: auto auto 13px;
  color: #bbe1fa;
`;
const ModalContainer = styled.div`
  margin: auto;
  position: relative;
  z-index: 2;
`;
const ModalBackdrop = styled.div`
  // Modal     CSS 
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.5);
`;

// TODO: useEffect      

const Header = () => {
  //  
  const [value, onChange] = useState(new Date());
  //   
  const [isModalOpen, setIsModalOpen] = useState(false);
  const modalHandler = (e) => {
    setIsModalOpen(true);
  };
  // useEffect    setIsModalOpen false     
  useEffect(() => {
    setIsModalOpen(false);
  }, [value]);
  // TODO:      moment().format("YYYY/MM/DD");

  return (
    <>
      <HeaderContainer onClick={modalHandler}>
        <DayToYear>{moment(value).format("YYYY MM DD")}</DayToYear>
        <DayOfWeek>{moment(value).format("dddd")}</DayOfWeek>
      </HeaderContainer>
      <ModalContainer>
        {isModalOpen && (
          <ModalBackdrop>
            <Calendar onChange={onChange} value={value} />
          </ModalBackdrop>
        )}
      </ModalContainer>
    </>
  );
};

export default Header;
